 @import '../assets/config';

.wp-block-data-tables-data-filter-donor-type {

	@include responsive-min( $break-small + 1px ) {
		display: flex;
		flex-direction: row;
		align-items: center;
		flex-wrap: wrap;
		column-gap: var( --wp--preset--spacing--20 );
		row-gap: var( --wp--preset--spacing--10 );
	
		background-color: var( --wp--preset--color--gray-200 );
		padding: var( --wp--preset--spacing--10 );
	
		input[type='radio'] {
			display: none;
		}
		
		label {
			display: inline-block;
			margin-right: 5px;
			cursor: pointer;
			background-color: var(--wp--preset--color--gray-200);
			transition: background-color 0.3s, color 0.3s;
			text-align: center;
	
			font-size: var( --wp--preset--font-size--small );
			font-weight: 400;
			padding: calc( var( --wp--preset--spacing--10 ) / 2 );
			border-radius: var( --wp--custom--border--radius--sm );
		
			&:hover,
			&:focus {
				background-color: var( --wp--custom--color--hover );
			}
		}
		
		input[type='radio']:checked + label {
			background-color: var( --wp--preset--color--contrast-2 );
			color: var( --wp--preset--color--white );
		}
	}

}
